.fade-out {
  animation: fadeOut 0.5s ease forwards;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.reveal{
  opacity: 0;
 
}

.reveal.active {
  opacity: 1;
}
.active.fade-top{
  animation: fade-top 1s ease-in-out forwards;

}
.active.fade-bottom {
  animation: fade-bottom 1s ease-in-out forwards;
}
.active.fade-left {
  animation: fade-left 1s ease-in forwards;
}
.active.fade-right {
  animation: fade-right 1s ease-in forwards;
}
@keyframes fade-bottom {
  0% {
    transform: scale(0);

    transform: translateY(100px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    transform: scale(1.1);

    opacity: 1;
  }
}
@keyframes fade-top {
  0% {
    transform: scale(0);
    transform: translateY(0);
    opacity: 0;
  }
  100% {
   
    transform: translateY(100px);
    transform: scale(1);


    opacity: 1;
  }
}

@keyframes fade-left {
  0% {
    transform: scale(0);

    transform: translateX(-200px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    transform: scale(1.1);

    opacity: 1;
  }
}

@keyframes fade-right {
  0% {
    transform: scale(0);

    transform: translateX(200px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    transform: scale(1.1);

    opacity: 1;
  }
}
