.navigationbar{
  position: relative;
  display: flex;
  justify-content: space-around;
}.navigationbars {
  padding: 10px 20px;
  color: #fff;
  cursor: pointer;
  position: relative;
  text-transform: uppercase;}

  
  
  .navigationbars.active    {
border-radius: 16px;
box-shadow: 0 4px 30px rgba(84, 84, 84, 0.041);
backdrop-filter: blur(6.3px);
-webkit-backdrop-filter: blur(6.3px);
border: 1px solid rgba(0, 0, 0, 0.83);

    background-color:rgba(90, 90, 90, 0.219);
    animation: underlineExpand 0.3s ease forwards; /* Animation for underline expansion */
  }
  
  @keyframes underlineExpand {
    from {
      transform: scaleX(0); /* Scale the underline horizontally */
    }
    to {
      transform: scaleX(1); /* Scale the underline to full width */
    }
  }
  .mobile-menu {
    display: none; /* Hide by default for larger screens */
  }
  .hamburger {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 37px;
    height: 19px;    cursor: pointer;
  }
  
  .hamburger span {
    margin-left: 8px;
    width: 65%;
    height: 2px;
    background-color: #fff;  }
  
  /* Styles for open hamburger menu */
  .hamburger.open span:nth-child(1) {
    transform: rotate(45deg) translate(2px, 6px);  }
  
  .hamburger.open span:nth-child(2) {
    opacity: 0;
  }
  
  .hamburger.open span:nth-child(3) {
    transform: rotate(-45deg) translate(2px, -7px);  }
  
  /* Mobile menu content */
  .mobile-menu-content {
    display: none; /* Hide by default for larger screens */
  }
  
